html {
    font-size: 100%;
    box-sizing: border-box;
}

body {
    font-family: 'Rubik', sans-serif;
    line-height: 2rem;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

.js .animate-this,
.js .animate-intro {
    opacity: 0;
}
.no-cssanimations .animate-this,
.no-cssanimations .animate-intro {
    opacity: 1;
}

h1,
h2,
h3 {
    font-family: 'Lora', serif;
    color: #fff;
    text-rendering: optimizeLegibility;
    margin: 0;
}

h2 {
    font-family: 'Rubik', sans-serif;
    color: #712426;
    margin-bottom: .5rem;
    letter-spacing: .15rem;
    text-transform: uppercase;
}

p {
    color: #4c4c65;
}

header {
    width: 100%;
    height: auto;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 700;
}

header .mh-logo {
    top: 25px;
    position: absolute;
    left: 25px;
    margin: 0;
    padding: 0;
}

header .mh-logo a {
    display: block;
    margin: 0;
    padding: 0;
    outline: 0;
    border: none;
    width: 55px;
    height: 55px;
    background: url(/images/mh-logo.png) no-repeat center;
    background-size: 55px 55px;
    font: 0/0 a;
    text-shadow: none;
    color: transparent;
}

#home {
    width: 100%;
    height: 100%;
    background-image: url(/images/fresno.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;
    min-height: 750px;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: .6;
    background-color: #000000;
}

.gradient {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: .2;
    background: -webkit-linear-gradient(left, black 0%, black 20%, transparent 100%);
    background: linear-gradient(to right, black 0%, black 20%, transparent 100%);
}

.home-content {
    width: 100%;
    height: 100%;
    display: table;
    position: relative;
    min-height: 702px;
}

.intro {
    height: 100%;
    width: 100%;
    vertical-align: middle;
    display: table-cell;
    padding: 4rem 2rem 2.5rem 2rem;
}

.hello {
    font-family: 'Rubik', sans-serif;
    margin: 0;
    font-size: 1.1rem;
    color: rgba(255, 255, 255, 0.55);
    letter-spacing: .15rem;
}

.headline {
    line-height: 1.45;
}

.headline h1 {
    margin-bottom: 1.5rem;
}

.home-links {
    font-family: 'Rubik', sans-serif;
    margin-top: 1.25em;
    margin-bottom: 0;
    color: rgba(255, 255, 255, 0.55);
    list-style: none;
    display: inline-block;
    padding: 0;
    letter-spacing: .1rem;
}

.home-links a,
li {
    display: inline-block;
    margin-bottom: .25rem;
    line-height: 1rem;
}

.home-links li {
    margin-right: 1.5rem;
    font-weight: bold;
    font-size: .75rem;
    width: 100%;
}

.home-links a {
    font-family: 'Lora', serif;
    font-size: 1.25rem;
    text-decoration: none;
    color: #fff;
    margin-left: .3rem;
    letter-spacing: .05rem;
}

#about {
    position: relative;
    padding: 2rem 0;
    min-height: 400px;
}

.about-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #ccc;
    width: 100%;
    background-image: url(/images/matt-about.png);
    background-repeat: no-repeat;
    background-size: cover;
    opacity: .4;
    z-index: -1;
}

.about-inner {
    width: 94%;
    height: 100%;
    margin: 0 auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.about-pic {
    margin: 0 0 .5rem 0;
    background-image: url(/images/matt-profile-pic.png);
    background-position: center;
    background-size: cover;
    border-radius: 50%;
    width: 10rem;
    height: 10rem;
}

.about-info {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    margin: 0;
    text-align: center;
}

.about-info p {
    text-align: left;
    line-height: 1.5rem;
    margin: .5rem 0;
}

.about-info h1,
h3 {
    color: #1A1A27;
    margin-bottom: .5rem;
}

.about-info h2 {
    letter-spacing: .15rem;
}

.button {
    display: inline-block;
    font-family: 'Rubik', sans-serif;
    font-size: 1.15rem;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: .2rem;
    height: 3.0rem;
    line-height: 3.0rem;
    width: 100%;
    margin: .25rem 0;
    background: #797979;
    color: #222;
    text-decoration: none;
    cursor: pointer;
    text-align: center;
    white-space: nowrap;
    border: none;
}

.resume-button {
    background: #1A1A27;
    color: #fff;
}

#portfolio {
    width: 100%;
    background: rgba(0, 0, 0, 0.075);
    text-align: center;
    padding: 2rem 0 2rem;
}

.portfolio-intro {
    padding: 0 1.5rem 0 1.5rem;
    margin-bottom: .75rem;
}

.portfolio-intro h1 {
    color: #1A1A27;
}

.portfolio-intro h2 {
    letter-spacing: .15rem;
}

.portfolio-content {
    box-sizing: inherit;
    margin: 0 auto;
    width: 100%;
    padding: 0 1.5rem 0 1.5rem;
}

.portfolio-content h2 {
    color: #712426;
    margin-bottom: 1rem;
    text-transform: uppercase;
    font-size: 1.75rem;
    text-shadow: 0px 0px 10px rgba(0, 0, 0, .3);
    text-align: left;
    letter-spacing: .05rem;
    line-height: 1.75rem;
}

.portfolio-list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.portfolio-list li {
    position: relative;
    margin: 3rem 0;
    display: list-item;
    text-align: left;
}

.portfolio-image {
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    float: none;
    clear: both;
    opacity: .2;
    z-index: -1;
}

.portfolio-description {
    width: 100%;
    padding: 1.5rem;
    position: relative;
}

.portfolio-description a {
    color: #A35213;
    text-decoration: none;
    font-family: 'Lora', sans-serif;
    font-size: 1.15rem;
    font-weight: bold;
}

.portfolio-description h3 {
    margin: .75rem 0;
    line-height: 1.75rem;
    font-size: 1.25rem;
}

.portfolio-description-list {
    padding-left: .75rem;
    font-family: 'Lora', sans-serif;
    list-style: disc;
}

.portfolio-description-list li {
    line-height: 1.5rem;
    margin: 1rem 0;
    font-size: 1.05rem;
    color: #1a1a27;
    font-family: 'Rubik', sans-serif;
    font-weight: bold;
}

.mu-blog-screenshot {
    background-image: url(/images/portfolio/mu-blog-screenshot.png);
}

.maps-screenshot {
    background-image: url(/images/portfolio/maps-screenshot.png);
}

.wtgg-screenshot {
    background-image: url(/images/portfolio/wtgg-screenshot.png);
}

#skills {
    background-image: url(/images/skills-bg.png);
    background-repeat: repeat;
    background-position: center;
    position: relative;
    padding: 2rem 0;
}

.skills-intro {
    text-align: center;
    padding: 0 1.5rem;
    margin-bottom: 1.25rem;
}

.skills-intro h1 {
    margin-bottom: 1.25rem;
    line-height: 3rem;
    font-size: 2.5rem;
}

.skills-intro h2 {
    letter-spacing: .15rem;
}

.skills-intro h3 {
    color: #fff;
    font-size: 1.15rem;
    font-weight: 400;
}

.skills-content {
    padding: 0 1.5rem;
    width: 100%;
    height: 100%;
}

.skills-list {
    padding: 0;
    margin: 0;
    font-family: 'Rubik', sans-serif;
    font-weight: bold;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    list-style: none;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.skills-list li {
    padding: 1rem;
    margin: .5rem;
    text-align: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    word-wrap: normal;
    color: #fff;
    letter-spacing: .07rem;
    font-size: 1.15rem;
    line-height: 1.5rem;
}

.skills-list img {
    height: 5rem;
    width: auto;
    padding-bottom: .5rem;
}

.skills-list figcaption {
    color: #fff;
    letter-spacing: .07rem;
    font-size: 1.15rem;
    line-height: 1.5rem;
}

#history {
    width: 100%;
    background: #f4f4f4;
    padding: 2rem 0;
}

.history-intro {
    padding: 0 1.5rem;
    margin-bottom: 2rem;
}

.history-intro h1 {
    color: #1a1a27;
    text-align: center;
    line-height: 3rem;
    font-size: 2.5rem;
}

.history-intro h2 {
    text-align: center;
}

.history-content {
    padding: 0 1.5rem;
    text-align: center;
}

.history-content h2 {
    font-size: 1.3rem;
    margin-bottom: 3rem;
}

.timeline-wrap {
    position: relative;
    text-align: left;
}

.timeline-wrap::before {
    content: "";
    display: block;
    width: 1px;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.1);
    position: absolute;
    left: 2rem;
    top: 0;
}

.timeline-entry {
    padding-left: 5rem;
    margin-bottom: 4rem;
    position: relative;
}

.timeline-icon {
    height: 3rem;
    width: 3rem;
    line-height: 3rem;
    background: #162041;
    border-radius: 50%;
    text-align: center;
    color: #FFFFFF;
    position: absolute;
    left: 2rem;
    top: 0;
    margin-left: -1.5rem;
}

.timeline-content p,
h3,
h4 {
    font-family: 'Rubik', sans-serif;
    margin: 0;
    margin-bottom: .25rem;
    line-height: 1.25;
}

.timeline-content p {
    font-weight: bold;
    opacity: .75;
}

.timeline-content h3 {
    font-size: 2rem;
}

.timeline-content h4 {
    font-size: 1.25rem;
}

.fa-building {
    margin-left: 2px;
}

#contact {
    background-color: #fff;
    background-image: url(/images/contact-bg.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 2rem 0;
    position: relative;
    text-align: center;
}

.contact-intro {
    padding: 0 1.5rem;
    margin-bottom: 1rem;
    position: relative;
}

.contact-intro h1 {
    color: #fff;
    text-align: center;
    line-height: 3rem;
    font-size: 2.5rem;
    margin-bottom: 1rem;
}

.contact-intro h3 {
    font-family: Rubik, sans-serif;
    margin: 0;
    color: #fff;
    margin-bottom: .25rem;
    line-height: 1.75;
}

.contact-content {
    padding: 0 1.5rem;
    margin-top: 1rem;
    position: relative;
}

.contact-content h2 {
    font-size: 1.25rem;
    color: #fff;
    letter-spacing: .15rem;
}

.contact-content a {
    font-size: 1.25rem;
    text-decoration: none;
    color: #c5494d;
    letter-spacing: .05rem;
    padding: 15px;
}

footer {
    width: 100%;
    background: #0d0d13;
    padding: 1.5rem 0;
    height: 200px;
}

.footer-links {
    margin: 0;
    padding: 0 1.5em;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.footer-links li {
    padding: .5rem;
    margin: .5rem;
}

.footer-links i {
    color: #8b94a7;
}

.copyright p {
    margin-top: 1.5rem;
    margin-bottom: 0;
    padding-bottom: .5rem;
    text-align: center;
    font-size: .8rem;
}


